<template>
<div>
  <VueUeditorWrap 
      v-model="message" 
      :config="editorConfig"
    />
  <input type="checkbox" v-model="scroll_y" >纵向滚动条禁用
  <input type="checkbox" v-model="scroll_x" >横向滚动条禁用<br>
  <span>标题:</span><input type="text" v-model="tittle"><br>
  <span>路径:public/</span><input type="text" v-model="filepath">
  <span>文件名:</span><input type="text" v-model="filename"><span>.html</span><br>
  <input type="button" value="生成页面文件" @click="submit">
</div>
</template>

<script>
/**
 * npm i vue-ueditor-wrap -S
 * 2.将下载的ueditor文件放入开放的公共文件夹中，当前为public
 * 3.修改路径：在文件 vue-ueditor-wrap/lib/vue-ueditor-wrap.min.js 中找到
 * defaultConfig:{UEDITOR_HOME_URL:t.env.BASE_URL?t.env.BASE_URL+"UEditor/": 冒号后面的修改为下载的ueditor文件存放路径
 */ 
import VueUeditorWrap from 'vue-ueditor-wrap' 
export default {
  data () {
    return {
      message:"",
      editorConfig: {
        // 初始容器宽度
        initialFrameWidth: '100%',
      },
      filepath:"",
      filename:"download",
      scroll_y:true,
      scroll_x:false,
      tittle:"私人资料站",
    }
  },
  methods: {
    submit(){
      //读取路径，用于修改导入的 js css 文件路径
      let patharr = this.filepath.split("/");
      let path = "";
      if(patharr.length==1&&patharr[0]==""){
        path = "./";
      }else if(patharr.length==1&&patharr[0]!=""){
        path = "../";
      }else if(patharr.length>1){
        for (let index = 0; index < patharr.length; index++) {
          const element = patharr[index];
          if(element!=""){path+="../"}
        }
      }
      //生成定时器调整iframe自动宽高语句
      let str = "";
      if(this.scroll_y||this.scroll_x){
        str="    SyntaxHighlighter.all();";//SyntaxHighlighter 代码高亮
        str+="    window.setInterval(function changeHeight(){ \n";
          if(this.scroll_y){
            str+="        window.frameElement.style.height=(window.document.body.scrollHeight+30)+'px';\n";
          }
          if(this.scroll_x){
            str+="        window.frameElement.style.width=window.document.body.scrollWidth+'px';\n";
          }
        str+="    },400);\n"
      }
      //修改页面，增加页面模板
      this.message= "<!DOCTYPE html>\n"
                    +"<html lang=\"en\">\n"
                    +"  <head>\n"
                    +"    <meta charset=\"utf-8\">\n"
                    +"    <meta http-equiv=\"X-UA-Compatible\" content=\"IE=edge\">\n"
                    +"    <meta name=\"viewport\" content=\"width=device-width,initial-scale=1.0\">\n"
                    +"    <link rel=\"stylesheet\" href=\""+path+"base.css\">\n"
                    +"    <link rel=\"stylesheet\" href=\""+path+"ueditor/third-party/SyntaxHighlighter/shCoreDefault.css\">\n"
                    +"    <title>"+this.tittle+"</title>\n"
                    +"  </head>\n"
                    +"  <body style=\"background:transparent;\">\n"
                    +this.message
                    +"  </body>\n"
                    +"  <script type=\"text/javascript\" src=\""+path+"ueditor/third-party/SyntaxHighlighter/shCore.js\"></"+"script>\n"
                    +"  <script type=\"text/javascript\">\n"
                    +str
                    +"  </"+"script>\n"
                    +"</html>\n"
      window.console.info(this.message);
      //利用HTML5的a标签下载属性
      var element = document.createElement('a');
      element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(this.message));
      element.setAttribute('download', this.filename+'.html');
      element.style.display = 'none';
      document.body.appendChild(element);
      element.click();
      document.body.removeChild(element);
    }
  },
  components: {
    VueUeditorWrap
  }
}

</script>

<style scoped>

</style>